<template>
    <div class="questionnaireItem">
        <!-- 单选 -->
        <div v-if="questionnaireCon.QUEST_TYPE_FLAG == 1 && questionnaireCon.SELECT_TYPE_FLAG == 1">
            <van-radio-group v-model="radio" disabled="">
                <van-radio  checked-color="#FB5768" :class="[item.selected?'checkedBox':'']" v-for="(item,index) in questionnaireCon.optionList" :name="item.ITEM_CODE" :key="index">{{item.ITEM_NAME}}</van-radio>
            </van-radio-group>
        </div>
        <!-- 多选 -->
        <div v-if="questionnaireCon.QUEST_TYPE_FLAG == 1 && questionnaireCon.SELECT_TYPE_FLAG == 0">
            <div v-if="isForbid == true">
                <van-checkbox-group v-model="result" disabled>
                      <van-checkbox  checked-color="#FB5768"  v-for="(item,index) in questionnaireCon.optionList" :key='index' :name="item.ITEM_CODE">{{item.ITEM_NAME}}</van-checkbox>
                </van-checkbox-group>
            </div>
            
            <div v-if="isForbid == false">
                 <van-checkbox-group v-model="result" disabled>
                    <van-checkbox  checked-color="#FB5768" :class="[item.selected?'checkedBox':'']" v-for="(item,index) in questionnaireCon.optionList" :key='index' :name="item.ITEM_CODE">{{item.ITEM_NAME}}</van-checkbox>
                </van-checkbox-group>
            </div>
        </div>

        
        <!-- 问答题 -->
        <div class="questionCon questionInput" v-if="questionnaireCon.QUEST_TYPE_FLAG == 0">
            <div class="answer">{{message}}</div>
        </div>

    </div>
</template>

<script>


export default {
    name:'QuestionnaireItem',
    props:{
        // 问卷题目对象
        questionnaireCon:{
            type:Object,
            default:{},
        },
        // 是否禁止选择
        isForbid:{
            type:Boolean,
            default:false
        }
    },
    data(){
        return{
            typeFlag:2,
            radio: '',
            result: [],
            message:''
        }
    },
    mounted(){
     
       this.setInit();
    },
    methods:{
        save(){
            let data = {};
            data.message = this.message;
            data.radio = this.radio;
            data.result = this.result;
            return data
        },
        setInit(){
           
            if(this.questionnaireCon.answerList && this.questionnaireCon.answerList.length > 0){
                if(this.questionnaireCon.QUEST_TYPE_FLAG == 1 && this.questionnaireCon.SELECT_TYPE_FLAG == 1){
                    for(let j=0;j<this.questionnaireCon.optionList.length;j++){
                            if(this.questionnaireCon.optionList[j].ITEM_CODE == this.questionnaireCon.answerList[0].ITEM_CODE)
                                this.questionnaireCon.optionList[j].selected = true;
                        }
                    this.radio = this.questionnaireCon.answerList[0].ITEM_CODE || '';
                };
                if(this.questionnaireCon.QUEST_TYPE_FLAG == 1 && this.questionnaireCon.SELECT_TYPE_FLAG == 0){
                    this.result = [];
                    for(let i=0;i<this.questionnaireCon.answerList.length;i++){
                        
                        for(let j=0;j<this.questionnaireCon.optionList.length;j++){
                            if(this.questionnaireCon.optionList[j].ITEM_CODE == this.questionnaireCon.answerList[i].ITEM_CODE)
                                this.questionnaireCon.optionList[j].selected = true;
                        }
                        this.result.push(this.questionnaireCon.answerList[i].ITEM_CODE || '')
                    }
                }
                if(this.questionnaireCon.QUEST_TYPE_FLAG == 0){
                    this.message = this.questionnaireCon.answerList[0].CONTENT || '';
                }
            }
        }
    }
}
</script>
<style>
.questionnaireItem{
    width: calc(100% - 24px);
    margin: 12px;
    height: auto;
}
.questionnaireItem .van-radio,.questionnaireItem .van-checkbox{
    height: 30px;
    line-height: 30px
}
.questionnaireItem .van-radio__label, .questionnaireItem .van-checkbox__label{
    font-size: 14px;
}
.questionnaireItem [class*=van-hairline]::after{
    outline: none;
    border: none
}
.questionnaireItem .van-field__control{
    background-color: #f8f8f8;
    height: 80px !important;
    color: #333333;
    -webkit-text-fill-color:#333333;
}
.questionnaireItem .van-cell{
    padding: 0 !important;
}
.questionnaireItem .questionCon{
    height: auto;
   
}
.questionnaireItem .questionInput{
    min-height: 90px;
}
.questionnaireItem .van-checkbox__icon--checked.van-checkbox__icon--checked .van-icon{
    color: #ffffff; 
}
.questionnaireItem .van-checkbox__icon--checked .van-icon-success, .questionnaireItem .van-radio__icon--checked .van-icon-success{
    background-color:#FB5768;
    border-color:#FB5768;
    color: #FFF !important;
}
.checkedBox .van-checkbox__label{
    color: #333333!important;
}
.checkedBox .van-radio__label{
    color: #333333!important;
}
.answer{
    background-color: #f8f8f8;
    height: 80px !important;
    color: #333333;
    -webkit-text-fill-color:#333333;
    font-size: 14px;

}
</style>